  }
  .last-game__bg {
    width: 74.3%;
  }
  .last-game__text {
    margin-left: 0;
  }
  .last-game__title {
    margin-top: 20px;
    margin-bottom: 10px;
  }
  .last-game__dsr {
    width: 455px;
